		const iState = workspace.editors.findIndex(ed => ed.id == stateFileId);
		const wallet: ArWallet = props.login.key;
		if (iState < 0 || !contractSrcTX) {
			throw Error(`Invalid state or contract id ${iState} ${contractSrcTX}`);
		}
