			if (!isNaN(value)) {
				if (Number.isInteger(value)) {
					value = parseInt(value);
				} else {
					value = parseFloat(value);
				}
			}
			if (d.key === 'function') {
				func = value;
			}
			fullPayload[d.key] = value;
